#serviceCard{
    grid-row: 2;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto;
    background-color: #597AEF;
    width: 250px;
    height: 300px; 
    margin: 30px;
    padding: 20px;
    color: white;
    border-radius: 20px;
}

#serviceCard a {
    grid-column: 2;
    grid-row: 1;
}

#toplineService {
    margin-bottom: -40px;
    grid-row:2;
    grid-column: 1 /span 2;
    font-size: large;
}

#textService {
    grid-column: 1 /span 2;
    grid-row: 3;
}